.Container {
  display: flex;
  justify-content: space-around;
  width: auto;
  height: 500px;
  border: 1px solid black;
  margin: 100px;
}

.DrumKeys {
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(4, 1fr);
  grid-gap: 3px;
  flex-grow: 1;
}

.Controls {
  flex-grow: 0.1;
  width: 200px;
  padding: 200px 20px; 
  border: 1px solid black;
}

.Q {
  grid-row: 1/2;
  grid-column: 1/2;
}

.A {
  grid-row: 2/3;
  grid-column: 1/2;
}

.Z {
  grid-row: 3/4;
  grid-column: 1/2;
}

.W {
  grid-row: 1/2;
  grid-column: 2/3;
}

.S {
  grid-row: 2/3;
  grid-column: 2/3;
}

.X {
  grid-row: 3/4;
  grid-column: 2/3;
}

.E {
  grid-row: 1/2;
  grid-column: 3/4;
}

.D {
  grid-row: 2/3;
  grid-column: 3/4;
}

.C {
  grid-row: 3/4;
  grid-column: 3/4;
}